.anime {
  font-weight: 300;
  display: block;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  grid-gap: 20px;
  text-decoration: none;
  color: inherit;
  text-align: center;
}

.anime__img {
  position: relative;
  height: 300px;
  margin-right: 30px;
  border-radius: 5px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.anime__title,
.anime__score,
.anime__year {
  font-weight: 300;
  text-decoration: none;
}

.anime__score{
  font-size: 35px;
  padding-right:10px;
}

.anime__title a{
  margin-bottom: 20px;
  font-size: 20px;
  color: #2c2c2c;
  text-decoration: none;
}

.anime__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}

.anime__genres li,
.anime__year {
  margin-right: 10px;
  font-size: 14px;
}

.anime__sub {
  display:flex;
}

@media screen and (max-width: 768px) {
  .anime__img {
  position: relative;
  width: 80%;
  height: auto;
  margin-right: 30px;
  border-radius: 5px;
}
  .anime__score{
    font-size:15px;
    padding-right:1px;
  }
  .anime__title a{
  font-size: 15px;
  color: #2c2c2c;
  text-decoration: none;
  transform: translate(-22%, -50%) scale(0.5);
  display: inline-block;
}
  .anime__title,
.anime__score,
.anime__year {
  font-weight: 100;
  height: 10px;
  text-decoration: none;
}

}